<template>
  <div>
    <el-card class="">
      <div slot="header">
        <span>分类管理</span>
      </div>
      <!-- 树形组件 -->
      <el-tree ref="tree" node-key="cate_id" :load="loadNode" :default-expanded-keys="[0]" highlight-current lazy
               :props="defaultProps">
        <div class="custom-tree-node" slot-scope="{ node, data }">
          <span>{{ node.label }}</span>
          <div>
            <el-button @click.stop="openEditDialog(node, data)" type="text" icon="el-icon-edit">
              编辑
            </el-button>
            <el-button @click.stop="openInsertDialog(node, data)" type="text" icon="el-icon-circle-plus-outline">
              添加
            </el-button>
            <el-button @click.stop="openRemoveDialog(node, data)" type="text" icon="el-icon-delete">
              删除
            </el-button>
          </div>
        </div>
      </el-tree>
    </el-card>
    <!-- 添加模态框 -->
    <el-dialog title="添加分类" :visible.sync="insert_dialog_visible" @close="handleCloseDialog('insert_form')">
      <el-form :model="insert_form" :rules="rules" ref="insertForm" status-icon label-width="80px">
        <el-form-item label="分类名称" prop="cate_name">
          <el-input v-model="insert_form.cate_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="insert_dialog_visible = false">取 消</el-button>
        <el-button type="primary" @click="handleInsert('insertForm')">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑模态框 -->
    <el-dialog title="编辑分类" :visible.sync="edit_dialog_visible" @close="handleCloseDialog('edit_form')">
      <el-form :model="edit_form" :rules="rules" ref="editForm" label-width="80px">
        <el-form-item label="分类名称" prop="cate_name">
          <el-input v-model="edit_form.cate_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="edit_dialog_visible = false">取 消</el-button>
        <el-button type="primary" @click="handleEdit('editForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Category } from "@/api/";

export default {
  name: "List",
  data() {
    return {
      defaultProps: { label: "cate_name" },
      insert_dialog_visible: false,
      insert_form: {
        cate_name: '',
        parent_id: '',
      },
      edit_dialog_visible: false,
      edit_form: {
        cate_id: "",
        cate_name: "",
        parent_id: "",
      },
      rules: {
        cate_name: [{ required: true, message: "请输入分类名称", trigger: "blur" }],
      },
      // 当前操作的node对象
      current_node: '',
    };
  },
  created() {
    document.title = "分类管理";
  },
  methods: {
    // 加载子级分类--懒加载
    async loadNode(node, resolve) {
      // 根节点
      let root_data = [{
        cate_id: 0,
        cate_name: "全部分类",
      }];
      if (node.level === 0) {
        return resolve(root_data);
      }
      // 子级节点
      let { cate_id } = node.data;
      let { status, data } = await Category.subcate({ cate_id });
      if (status) {
        resolve(data);
      }
    },
    // 打开编辑Dialog
    openEditDialog(node, data) {
      // 缓存当前操作的node
      this.current_node = node;
      // 还原表单
      this.edit_form = { ...data };
      // 打开dialog
      this.edit_dialog_visible = true;
    },
    handleEdit(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let { status, msg } = await Category.edit({ ...this.edit_form });
          if (status) {
            // 操作DOM
            Object.assign(this.current_node.data, this.edit_form);
            // 关闭弹窗
            this.edit_dialog_visible = false;
            // 信息提示
            this.$message.success(msg);
          } else {
            // 信息提示
            this.$message.error(msg);
          }
        }
      });
    },
    // 打开添加Dialog
    openInsertDialog(node, data) {
      // 缓存当前操作的node
      this.current_node = node;
      // 转存parent_id
      this.insert_form.parent_id = data.cate_id;
      // 打开dialog
      this.insert_dialog_visible = true;
    },
    handleInsert(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let { status, msg, data } = await Category.insert({ ...this.insert_form });
          if (status) {
            // 操作DOM--浅拷贝
            let new_child = { ...this.insert_form, ...data };
            this.$refs.tree.append(new_child, this.current_node);
            // 关闭弹窗
            this.insert_dialog_visible = false;
            // 信息提示
            this.$message.success(msg);
          } else {
            // 信息提示
            this.$message.error(msg);
          }
        }
      });
    },
    openRemoveDialog(node, data) {
      let { cate_id } = data;
      this.$confirm("确定要删除此分类吗？", { type: "warning" })
      .then(async () => {
        let { status, msg } = await Category.remove({ cate_id });
        if (status) {
          // 操作DOM
          this.$refs.tree.remove(data);
          // 信息提示
          this.$message.success(msg);
        } else {
          this.$message.error(msg);
        }
      })
      .catch(() => {
        this.$message.info("取消成功！");
      });
    },
    // 关闭弹窗，清除表单验证状态
    handleCloseDialog(formName) {
      this.$refs[formName].resetFields();
    },
    //设置默认展开节点
    setDefaultExpandedKeys() {
      // 获取树形组件实例
      let allNodes = this.$refs.tree.store._getAllNodes();
      let defaultExpandedNodes = [];
      allNodes.forEach(node => {
        node.expanded && defaultExpandedNodes.push(node.data.id);
      });
      this.defaultExpandedKeys = defaultExpandedNodes;
    },
  },
};
</script>

<style>
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 13px;
  padding-right: 8px;
}
</style>
